/* black: 191414 */
/* green: 1db954 */

.SideDrawer {
  width: 0%;
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  overflow-y: scroll;
  color: white;
  padding: 2.5rem 0 0 2.5rem;
  font-size: 1.4rem;
  background-color: #191414;
  scrollbar-width: none;
  z-index: 100;
  animation: comeIn 0.5s ease-in both;
  outline: 900px solid rgba(0, 0, 0, 0.5);
  border-right: 1px solid rgba(255, 255, 255, 0.1);
}

.SideDrawer:after {
  content: '';
  width: 35%;
}

.SideDrawer::-webkit-scrollbar {
  display: none;
}

.SideDrawer ul {
  margin: 1rem 0 4rem 0;
}

.SideDrawer li {
  font-weight: 200;
  list-style: none;
  margin: 0.5rem 0;
}

.SideDrawer a {
  color: white;
  text-decoration: none;
}

.TopNav {
  margin-top: 5rem;
}

.TopList {
  margin: 0 0 4rem 0;
}

.Title {
  font-size: 1.8rem;
  text-transform: uppercase;
  font-weight: 300;
}

.TopList li {
  margin: 1.5rem 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.TopList a {
  margin-left: 1.6rem;
  font-weight: 500;
  font-size: 1.4rem;
  transform: translateY(0.1rem);
}

.MidList a {
  font-weight: 500;
}

.Active {
  display: inline-block;
  border-left: 4px solid #1db954;
  padding-left: 2.1rem;
  transform: translateX(-2.5rem);
}

.Active a {
  margin-left: 1.6rem;
}

.TopNav,
.Title,
.MidList {
  opacity: 0;
  animation: fade 0.1s 0.5s forwards;
}

@keyframes comeIn {
  0% {
    width: 0%;
  }
  100% {
    width: 35%;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
